/*
* 表格滚动展示插件
* $('#yourtable').tableScroll({
*   during: 500,  //动画时间
*   interval: 2000  //滚动间隔
* });
* 注意：如果匹配多个表格，只有第一个被匹配的被激活滚动效果，如需多个表格滚动，需要多次调用。
* */
;(function($){

    function init($table){
        $table.css({
            position: 'relative',
            top: 0
        });
    }

    $.fn.tableScroll = function(option){
        var $box, $table, $tr, $td;
        var timer, opt;

        $box = this.parent();
        $table = this.eq(0);
        $tr = $table.find('tr');
        $td = $tr.children('td');

        opt = $.extend({
            during: 500,
            interval: 2000
        }, option);

        init($table);

        timer = setInterval(function(){
            if(navigator.userAgent.match(/(MSIE 8.0|MSIE 7.0|MSIE 6.0)/g)) {
                $table.animate({
                    top: -$td.height()
                }, opt.during, function(){
                    $table.css('top', 0).find('tr:first-child').appendTo($table);
                });
            } else {
                $table.css({
                    transition: 'transform .5s',
                    transform: 'translateY(-'+$td.height()+'px)'
                });
                setTimeout(function(){
                    $table.css({
                        transition: 'none',
                        transform: 'translateY(0px)'
                    }).find('tr:first-child').appendTo($table);
                }, opt.during);
            }
        }, opt.interval);
    };
})(jQuery);