/*
* 环形进度条插件
* */
;(function($){

    function ring(origin, option){
        option.origin = $(origin);
        option.percent = $(origin).html().replace(/[^\d\.]/g, '');
        this.option = option;

        this.box = $('<div></div>');
        this.box.css({
            overflow: 'hidden',
            display: 'inline-block',
            width: option.size,
            height: option.size,
            verticalAlign: 'middle'
        });
        this.svg = Raphael(this.box.get(0),option.size,option.size);

        option.origin.after(this.box);
    }

    ring.prototype.draw = function(){
        var percent = this.option.percent;
        if(this.option.percent >= 100) {
            percent = 99.99;
        }
        if(this.option.percent <= 0) {
            percent = 0.01;
        }

        var r = this.option.size / 2;
        var p = percent / 100;

        //画背景
        var bg = this.svg.circle(r, r, r - this.option.strokeWidth);
        bg.attr({
            fill: '#ffffff',
            stroke: '#eeeeee',
            'stroke-width': this.option.strokeWidth
        });


        //画圆环
        var endX = r+ Math.sin(p*2*Math.PI)*(r-this.option.strokeWidth);
        var endY = r - Math.cos(p*2*Math.PI)*(r-this.option.strokeWidth);
        var ring = this.svg.path([
            ['M', r, this.option.strokeWidth],
            ['A', r - this.option.strokeWidth, r - this.option.strokeWidth, 0, p > 0.5 ? 1 : 0, 1, endX, endY]
        ]);
        ring.attr({
            stroke: this.option.percent >= 100 ? '#2e9ee6' : '#f59e48',
            'stroke-width': this.option.strokeWidth
        });

        //文字
        var text = this.svg.text(r, r, this.option.percent + '%');
        text.attr({
            fill: this.option.percent >= 100 ? '#666666' : '#f59e48'
        });
    };

    $.fn.ringProcess = function(option){
        this.css({display: 'none'});
        option = $.extend({
            size: 45,
            strokeWidth: 3,
            fontSize: 14
        }, option);

        $.each(this, function(){
            var obj = new ring(this, option);
            obj.draw();
        });
    };
})(jQuery);