/*
* 轮播插件
* HTML:
* <div id="yourslider">
*     <ul>
*         <li><a href="#" style="background-image:url(图片路径);"></a></li>
*         ...
*         <li><a href="#" style="background-image:url(图片路径);"></a></li>
*     </ul>
* </div>
* CSS:
* 位于sass/common/_plugins.scss中
* JS:
* $('#yourslider').slider(option);
* */
;(function($){

    function slider(that, option){
        var _this = this;
        this.option = option;
        this.$box = $(that);
        this.$ul = $(that).children('ul');
        this.$li = this.$ul.children('li');

        this.$li_extra = this.$li.eq(0).clone().appendTo(this.$ul);  //增加无缝滚动余冗元素
        //增加事件
        var $indicator = $('<ul></ul>').addClass('slider-indicator').appendTo(this.$box);
        this.$indicator = $indicator;
        for(var i = 0, l = this.$li.length; i < l; i++){
            $('<li></li>').appendTo(this.$indicator).on('click', function(event){
                event.preventDefault();
                _this.slideTo($indicator.children('li').index(this));
            });
        }
        $indicator.children('li').eq(0).addClass('active');
        //只有一项时隐藏指示器
        if(this.$li.length <= 1){
            $indicator.remove();
        }

        this.timer = null;
        this.interval = null;
    }

    slider.prototype.initStyle = function(){
        this.$box.addClass('slider-box');
        this.$box.children('ul').eq(0).addClass('slider');

        var boxWidth = this.$box.width();
        this.$ul.css({
            position: 'relative',
            width: boxWidth * (this.$li.length + 1)
        });
        this.$li.css({
            width: boxWidth
        });
        this.$li_extra.css({
            width: boxWidth
        })
    };

    slider.prototype.slideTo = function(index){
        var that = this;

        clearTimeout(this.timer);
        clearInterval(this.interval);

        this.$indicator.children('li.active').removeClass('active');
        if(index < this.$li.length){
            this.$indicator.children('li').eq(index).addClass('active');
        } else {
            this.$indicator.children('li').eq(0).addClass('active');
        }

        if(navigator.userAgent.match(/(MSIE 8.0|MSIE 7.0|MSIE 6.0)/g)){  //兼容IE8
            this.$ul.animate({
                left: -this.$li.width() * index
            }, this.option.during);
        } else {  //现代浏览器
            this.$ul.css({
                transition: 'transform ' + this.option.during/1000 + 's',
                transform: 'translateX(-' + this.$li.width() * index + 'px)'
            });
        }
        this.timer = setTimeout(function(){
            that.$ul.css({  //关闭过渡效果
                transition: ''
            });
            if(index >= that.$li.length){
                if(navigator.userAgent.match(/(MSIE 8.0|MSIE 7.0|MSIE 6.0)/g)){  //兼容IE8
                    that.$ul.css({
                        left: 0
                    });
                } else {  //现代浏览器
                    that.$ul.css({
                        transform: 'translateX(0px)'
                    });
                }
            }
            that.autoSlide();
        }, this.option.during);
    };

    slider.prototype.autoSlide = function(){
        //只有一项时不轮播
        if(this.$li.length <= 1) {
            return;
        }

        var that = this;
        clearInterval(this.interval);
        this.interval = setInterval(function(){
            var current = that.$indicator.children('li').index(that.$indicator.children('li.active'));
            current++;
            that.slideTo(current);
        }, this.option.interval);
    };

    slider.prototype.reload = function(){
        this.initStyle();
        this.$li_extra.css('width', this.$box.width());
        this.$indicator.children('li.active').removeClass('active');
        this.$indicator.children('li').eq(0).addClass('active');
        if(navigator.userAgent.match(/(MSIE 8.0|MSIE 7.0|MSIE 6.0)/g)){  //兼容IE8
            this.$ul.css({
                left: 0
            });
        } else {  //现代浏览器
            this.$ul.css({
                transform: 'translateX(0px)'
            });
        }
    };

    $.fn.slider = function(option){
        //初始化数据
        option = $.extend({
            interval: 5000,
            during: 1500
        }, option);

        $.each(this, function(){
            var temp = new slider(this, option);
            temp.initStyle();
            temp.autoSlide();
            $(this).on('resize', function(){
                temp.reload();
            });
            $(window).on('resize', function(){
                temp.reload();
            });
        });

    };
})(jQuery);